<template>
  <aside>
    <div class="Cart">
  <h1 class="CartH1">
   <span class="CartIcon">
    Cart
    <i class="icofont-cart-alt icofont-1x"></i>
   </span>
   <button @click="toggle"> &times; </button>
  </h1>
  <div class="table">
    <table class="tables" >
     <thead>
       <tr>
        <th><span>Product image</span></th>
        <th>Products</th>
        <th>Price</th>
        <th>Qty</th>
        <th>Total</th>
       </tr>
     </thead>
     <tbody>
      <tr v-for="(product, i) in inventory" :key="i">
       <td><i class="icofont-carrot icofont-3x"></i></td>
       <td>{{ product.name}} </td>
       <td :value="price">{{product.price}}</td>
       <td :value="quantity" class="center">{{product.quantity}}</td>
       <td></td>
       <td>
        <button @click="addNewTodo(product.id)">
          &times;
        </button>
       </td>
      </tr>
     </tbody>
    </table>
    <p>No items in cart</p>
    <div class="Items">
     <span><strong>Total:</strong></span>
     <button > Checkout</button>
    </div>
   </div>
  </div>
</aside>
</template>

<script>
export default {
  name: 'myBar',
  props: [
    'toggle',
    'inventory',
    'cart',
    'value'
  ],
  methods: {
    addNewTodo (id) {
      this.$emit('add-total', id)
    }
  },
  emits: ['add-total']
}
</script>
