<div class="container">
 <div class="columns" v-for="(product , i) in inventory.slice(0, 3)" :key="i">
    <ul class="price">
     <li class="header">{{product.name}}</li>
     <div class="class">
     <li class="grey">Price:<span>{{product.price}}</span></li>
     <li>Storage: <span>10GB Storage</span></li>
     <li style="align-items:center ;">
     <label>Quantity: </label>    <input v-model.number="product.quantity" type="number"></li>
     </div>
     <li class="grey"><a href="#" class="button" @click="addToCart(product.name , i)">Sign Up</a></li>
    </ul>
  </div>
</div>
<div class="main-tasks">
<Tasks :tasks="tasks" @delete-task="deleteTask" @toggle-reminder="toggleReminder" @add-task="addTask" ></Tasks>
</div>
   <myBar
   v-if="showNavbar"
   :toggle="toggleNavbar"
   :cart="cart"
   :inventory="inventory"
   @add-total="addNewTodo"
/>
<div class="addTask">
<AddTask @add-task="addTask" v-show="showAddTask" />
<myButton
@toggle-add-task="toggleAddTask"
/>
</div>

