<script>

import myBar from './components/Sidebar.vue'
import Slideshow from './components/Slideshow.vue'
import Tasks from './components/Tasks.vue'
import AddTask from './components/AddTask.vue'
import myButton from './components/Button.vue'

export default ({
  name: 'App',
  components: { myBar, Slideshow, Tasks, AddTask, myButton },
  data () {
    return {
      showNavbar: false,
      inventory: [],
      cart: {},
      newTodoText: '',
      count: 0,
      tasks: [],
      showAddTask: false
    }
  },
  methods: {
    toggleNavbar () {
      this.showNavbar = !this.showNavbar
    },
    toggleAddTask () {
      this.showAddTask = !this.showAddTask
    },
    addToCart (name, index) {
      this.cart[name] = this.inventory[index].quantity
      console.log(this.cart)
    },
    addNewTodo () {
      let total = 0; for (const p of this.cart) { total += (p.price * p.quantity) } return total
    },
    addTask (task) {
      this.tasks = [...this.tasks, task]
    },
    deleteTask (id) {
      if (confirm('Are you sure?')) {
        this.tasks = this.tasks.filter((task) => task.id !== id)
      }
    },
    toggleReminder (id) {
      this.tasks = this.tasks.map((task) => task.id === id ? { ...task, reminder: !task.reminder } : task)
    }
  },
  created () {
    this.inventory = [
      { id: 1, name: 'Raddishes', price: 3.26, type: 'vegetable', quantity: 0 },
      { id: 2, name: 'Artichokes', price: 9.44, type: 'vegetable', quantity: 0 },
      { id: 3, name: 'Broccoli', price: 5.20, type: 'vegetable', quantity: 0 }
    ]
    this.tasks = [
      {
        id: 1,
        text: 'Doctor appoinment',
        day: 'March 3',
        reminder: true
      },
      {
        id: 2,
        text: 'Teacher appoinment',
        day: 'May 3',
        reminder: true
      }
    ]
  }
})
</script>

